import { Component, OnInit,EventEmitter } from '@angular/core';
import {CKEditor} from 'ng2-ckeditor';
import {FileUploaderComponent} from '../ckeditor/file-uploader/file-uploader.component';

@Component({
  moduleId: module.id,
  selector: 'app-ckeditor',
  templateUrl: 'ckeditor.component.html',
  styleUrls: ['ckeditor.component.css'],
  directives:[CKEditor,FileUploaderComponent],
  outputs: ['valueChanged'],
  inputs: ['htmlValue','fileUploader','PATH','INDEX','imgParams'],
})
export class CkeditorComponent implements OnInit {
  fileUploader:boolean=false;
  imagelistShow:boolean=false;
  valueChanged = new EventEmitter<string>();
  htmlValue:string;
  constructor(){

  }
  editorReady(event){
    console.log('ready');

    console.log(event);
  }
  change(event){
    this.valueChanged.emit(event);
  }
  ngOnInit(){

  }

  onSubmitModelForm(form){
    console.log(form);
  }

  onSubmitTemplateForm(form){
    console.log(form);
  }

}